import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { CategoryService } from 'src/app/service/category.service';
declare var swal: any;

@Component({
  selector: 'app-category-add',
  templateUrl: './category-add.component.html'
})
export class CategoryAddComponent implements OnInit {

  loadingState = false;
  addForm: FormGroup;
  selectedFile: File;
  editImageUrl: any = null;

  showLoader = false;
  formErrors = {
    name: null,
    apierror: null
  };
  isEditing = false;
  editId = null;


  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private fb: FormBuilder,
    private categoryService: CategoryService
  ) {
    if (this.activatedRoute.snapshot.paramMap.get('id')) {
      this.isEditing = true;
      this.editId = this.activatedRoute.snapshot.paramMap.get('id');
      this.editObject();
    }
    this.addForm = fb.group({
      name: [null, Validators.compose([Validators.required])],
    });
  }

  ngOnInit() {

  }

  editObject() {
    this.loadingState = true;
    this.categoryService.getCategoryById(this.editId).subscribe((response) => {
      this.loadingState = false;
      if (response.data) {
        this.editImageUrl = response.data.image,
          this.addForm.patchValue({
            name: response.data.name,
          });
      }
    }, (error) => {
      this.loadingState = false;
      this.router.navigateByUrl('/category');
    });
  }

  fileChangeEvent(fileInput: any) {
    const reg = /(.*?)\.(jpg|jpeg|png|gif|giff)$/;
    if (!fileInput.target.files[0].name.match(reg)) {
      swal.fire(
        'Invalid File!',
        'Please select valid file ',
        'error'
      );
      this.removeFile();
      return false;
    } else {
      this.removeFile();
      this.selectedFile = fileInput.target.files[0];
    }
  }

  removeFile() {
    this.selectedFile = null;
  }

  submitForm(formdata): void {
    if (this.addForm.valid) {
      this.showLoader = true;
      if (this.isEditing) {
        this.categoryService.editCategory(this.editId, formdata.value).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.router.navigateByUrl('/category');
          } else {
            this.showLoader = false;
            response.error.map(obj => {

              if (obj.hasOwnProperty('name')) {
                this.formErrors.name = obj.name;
              }

            });
          }
        });
      } else {
        this.categoryService.addCategory(formdata.value).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.router.navigateByUrl('/category');
          } else {
            this.showLoader = false;
            response.error.map(obj => {

              if (obj.hasOwnProperty('name')) {
                this.formErrors.name = obj.name;
              }
            });
          }
        });
      }
    }
  }
}
